import "./App.css";
import React from "react";
import Movies from "./movies/movies";
import Boxoffice from "./boxoffice/boxoffice";
import {Routes, Route, useNavigate, Link} from "react-router-dom";
import {debounce} from "lodash";
import {BarsOutlined} from "@ant-design/icons";
import Login from "./login/login";
import KakaoLogin from "./login/kakaoLogin";
import LogOut from "./login/logout";
import KakaoLogout from "./login/test";
import {
    Drawer,
    Descriptions,
    Select,
    Collapse,
    Space,
    Button
} from 'antd';

function App() {
    const navigate = useNavigate(); //v6 useHistory  useNavigate 
    const [searchValue, setSearchValue] = React.useState("");
    const [selected, setSelected] = React.useState("");
    const [open, setOpen] = React.useState(false);
    const [placement, setPlacement] = React.useState('left');

    let ACCESS_TOKEN = localStorage.getItem("token");

    const {Panel} = Collapse;

    const showDrawer = () => {
        setOpen(true);
    };
    const onClose = () => {
        setOpen(false);
    };

    const onChange = debounce((e) => {
        setSearchValue(e.target.value);
    }, 300);

    const handleChange = (value) => {
        console.log(`selected ${value}`);
        setSelected(value);
    };

    return (
        <div>
            <header className="header">
                <div className="left-section">
                    <div className="hamburger-menu">
                        <BarsOutlined onClick={showDrawer}/>
                        <Drawer
                            title="    "
                            placement={placement}
                            width={500}
                            onClose={onClose}
                            open={open}
                            extra={<Space > <Button onClick={onClose}></Button>
                        </Space>}>
                            <h2> </h2>
                            <ul>
                                <li> api     </li>
                                <br/>
                                <li> api    </li>
                                <br/>
                                <li>React  SPA </li>
                                <br/>
                                <li>    api </li>
                                <br/>
                                <li>antd  </li>
                                <br/>
                                <li>
                                    <a href="https://osw6858.github.io/Go-movie/">    </a>
                                </li>
                            </ul>
                            <br/>
                            <h2></h2>
                            <ul>
                                <li>   14</li>
                                <br/>
                                <li>3/30 -  </li>
                                <br/>
                                <li>3/31 ~ 4/1 - api ,   </li>
                                <br/>
                                <li>4/2 ~ 4/7 -  </li>
                                <br/>
                                <li>4/8 ~ 4/10 -   api </li>
                                <br/>
                                <li>4/11 ~ 4/14 -     </li>
                            </ul>
                            <br/>

                        </Drawer>
                    </div>
                    <div>
                        <Link
                            to="/Go-movie"
                            style={{
                                textDecoration: "none",
                                color: "black"
                            }}>
                            <span className="logo">
                                go-movie
                            </span>
                        </Link>
                    </div>
                </div>
                <div className="middle-section">

                    <input
                        className="search-bar"
                        type="text"
                        placeholder="  ."
                        onChange={(e) => onChange(e)}/>
                    <Select
                        defaultValue="movieNm"
                        className="selector"
                        onChange={handleChange}
                        options={[

                            {
                                value: 'movieNm',
                                label: ''
                            }, {
                                value: 'directorNm',
                                label: ''
                            }
                        ]}/>

                </div>
                <div className="right-section">
                        {
                            ACCESS_TOKEN !== null
                                ? (
                                    <button
                                        onClick={() => {
                                            navigate("/logout");
                                        }}
                                        className="login-out-button">
                                        
                                    </button>
                                )
                                : (
                                    <button
                                        onClick={() => {
                                            navigate("/login");
                                        }}
                                        className="login-out-button">
                                        
                                    </button>
                                )
                        }
                </div>
            </header>
            <div className="body">

                <Routes>
                    <Route
                        path="/Go-movie"
                        element={<Movies searchString = {
                            searchValue
                        }
                        selected = {
                            selected
                        } />
}/>
                    <Route path="/login" element={<Login />}/>
                    <Route path="/login/kakaoLogin" element={<KakaoLogin></KakaoLogin>}/>
                    <Route path="/logout" element={<LogOut></LogOut>}/>
                    <Route path="/kakaologout" element={<KakaoLogout></KakaoLogout>}/>
                </Routes>
                <hr className="hr-line"></hr>
                <Boxoffice></Boxoffice>

            </div>
            <footer >
                <Descriptions title="User Info" className="footer">
                    <Descriptions.Item label="Made By">OH SHIN WOONG</Descriptions.Item>
                    <Descriptions.Item label="Telephone">010-3788-6858</Descriptions.Item>
                    <Descriptions.Item label="Live">Seoul</Descriptions.Item>
                    <Descriptions.Item label="Version">1.0</Descriptions.Item>
                   
                </Descriptions>
            </footer>
        </div>
    );
}

export default App;

