import "./movies.css";
import {Card, Result} from "antd";
import React from "react";
import axios from "axios";
import Pagination from "../pagenation/pagenation";
import {MOVIE_KEY} from "../key";
import dayjs from "dayjs";

function Movies(prop) {
    const [movies, setMovies] = React.useState([]);
    const limit = 8; //    
    const [page, setPage] = React.useState(1);
    const offset = (page - 1) * limit; //    (index)
    let searching = prop.searchString;
    let select = prop.selected;

    let isSelect;
    if (select === "movieNm") {
        isSelect = "movieNm";
    } else if (select === "directorNm") {
        isSelect = "directorNm";
    } else {
        isSelect = "movieNm"
    }

    let ACCESS_TOKEN = localStorage.getItem("token");
    //console.log("", searching);
    const url = `http://kobis.or.kr/kobisopenapi/webservice/rest/movie/searchMovieList.json?key=${MOVIE_KEY}&curPage=1&itemPerPage=50&${isSelect}=${searching}`;
    let date = new Date();

    React.useEffect(function () {
        axios
            .get(url)
            .then(function (result) {
                console.log("", result);
                const movies = result.data.movieListResult.movieList;
                if (ACCESS_TOKEN !== null) {
                    setMovies(movies);
                }
                if (ACCESS_TOKEN === null) {
                    const filterMv = movies.filter((param) => param.repGenreNm !== "()");
                    setMovies(filterMv);
                }
            })
            .catch(function (err) {
                console.log("", err);
            });
    }, [prop.searchString, isSelect]);

    if (movies.length === 0) {
        return (
            <div className="loading">
                <Result title=" !"></Result>
            </div>
        );
    }

    return (
        <div>
            <div className="info-title">
                <h1></h1>
                <h3>  - {dayjs(date).format("YYYY MM DD")}</h3>
            </div>
            <section className="info-grid">
                {
                    movies
                        .slice(offset, offset + limit)
                        .map((movies, index) => {
                            //slice index 0 8  map 
                            return (
                                <div key={index}>
                                    <Card title={`${movies.movieNm}`} type="inner" className="info-card">
                                        <p> : {movies.genreAlt}</p>
                                        <p> : {movies.repNationNm}</p>
                                        <p> : {movies.prdtYear}</p>
                                        <p> : {movies.prdtStatNm}</p>
                                        <p> : {
                                                movies
                                                    .directors
                                                    .map((name) => {
                                                        return name.peopleNm
                                                    })
                                            }</p>
                                    </Card>
                                </div>
                            );
                        })
                }
            </section>
            <div className="pagination">
                <Pagination total={movies.length}
                    //pagenation prop 
                    limit={limit} page={page} setPage={setPage}/>
            </div>
        </div>
    );
}

export default Movies;

